<template>
    <div class="fly-alone">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>飞单同步账户管理</span>
                    <div class="list-poptip">
                        <Checkbox style="font-size: 14px;" @on-change="hanleSyncTime" v-model="flyInfo.AutoSyn">打开同步时自动修正封盘时间，提前于收单盘口<InputNumber :min="0" v-model="flyInfo.AdvanceTime" class="column-input" style="width: 60px"></InputNumber>秒</Checkbox>
                        <div style="display: inline-block;font-size: 14px;">
                            飞单开启
                            <switchs style="verify-align: middle" :size="'big'" :open="flyInfo.OpenSheet" @switch="handleSwitch(flyInfo.OpenSheet)"></switchs>
                        </div>
                        <div style="margin-left: 8px;" class="button" @click="handleSyncInfo">
                            <span>飞单同步</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="query-list">
                <div class="connection-info">
                    <div class="connection-item">
                        <span>连接安全码</span>
                        <i-input v-model="flyInfo.SeurityNo" class="column-input"></i-input>
                    </div>
                    <div class="connection-item">
                        <span>房间号</span>
                        <i-input v-model="flyInfo.RoomNum" class="column-input"></i-input>
                    </div>
                    <div class="connection-item">
                        <span>登录账号</span>
                        <i-input v-model="flyInfo.MerchantName" class="column-input"></i-input>
                    </div>
                    <div class="connection-item">
                        <span>登录密码</span>
                        <i-input v-model="flyInfo.Password" type="password" class="column-input"></i-input>
                    </div>
                    <div v-show="!isLoginStatus" class="connection-item button login" @click="handleVerifyConnect">
                        <span style="margin-right: 0">登录</span>
                    </div>
                    <div v-show="isLoginStatus" class="connection-item button exit" @click="handleLongExit">
                        <span style="margin-right: 0">退出</span>
                    </div>
                </div>
                <div class="sync-info">
                    <div class="sync-item">
                        <p>收单盘口（高级账户）</p>
                        <p v-text="sync.handicap"></p>
                    </div>
                    <div class="sync-item">
                        <p>飞单账号</p>
                        <p v-text="sync.account"></p>
                    </div>
                    <div class="sync-item">
                        <p>账户余额</p>
                        <p v-text="sync.balance"></p>
                    </div>
                    <div class="sync-item">
                        <p>今日已结算金额</p>
                        <p v-text="sync.alreadyKnotBalance"></p>
                    </div>
                    <div class="sync-item">
                        <p>今日未结算金额</p>
                        <p v-text="sync.unfinishedBalance"></p>
                    </div>
                    <div class="sync-item">
                        <p>今日盈亏</p>
                        <p v-text="sync.profitAndLoss"></p>
                    </div>
                </div>
            </div>
        </div>
        <div class="other-nav">
            <div class="nav-item">
                <span>飞单列表</span>
            </div>
            <Checkbox style="font-size: 14px;" @on-change="handleTipsStatus" v-model="flyInfo.Remind">飞单账户余额低于<InputNumber :min="0" v-model="flyInfo.LowFraction" class="column-input" style="width: 120px"></InputNumber>声音提示</Checkbox>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>飞单同步列表</span>
                    <div class="list-poptip">
                        <span>说明：开启飞单的玩家注单会进入列表，禁止取消后进行同步。</span>
                    </div>
                </div>
            </div>
            <div class="list-data">
                <div class="data-header">
                    <p class="item-info item-1">玩家</p>
                    <p class="item-info item-2">游戏</p>
                    <p class="item-info item-4">期号</p>
                    <p class="item-info item-5">注单内容</p>
                    <p class="item-info item-6">注单金额</p>
                    <p class="item-info item-7">注单时间</p>
                    <p class="item-info item-8">状态</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in showDataList" :key="index">
                        <p class="item-info item-1">{{item.username}}</p>
                        <p class="item-info item-3">{{item.gameName}}</p>
                        <p class="item-info item-4">{{item.perids}}</p>
                        <p class="item-info item-5" :title="item.currentBet">{{item.currentBet}}</p>
                        <p class="item-info item-6">{{item.bet}}</p>
                        <p class="item-info item-7">{{item.time}}</p>
                        <p class="item-info item-8">{{item.status}}</p>
                    </div>
                </div>
            </div>
            <Page v-show="page.total" class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
    </div>
</template>

<script>
import switchs from '../../component/switch.vue';
import Bus from '../../tools/eventBus/index.js';
export default {
    components: {
        switchs
    },
    data() {
        return {
            flyAloneMana: {
                openUse: true,
                useSecond: 20,
                isOpenSync: false
            },
            connectionInfo: {
                code: '',
                account: '',
                password: ''
            },
            sync: {
                handicap: '',
                account: '',
                balance: '',
                alreadyKnotBalance: 0,
                unfinishedBalance: 0,
                profitAndLoss: 0
            },
            dataList: [],
            isLoading: false,
            isLoading1: false,
            flyInfo: {
                userId: '',
                targetId: '',
                AdvanceTime: 20,
                SeurityNo: '',
                AutoSyn: false,
                Password: '',
                Remind: false,
                OpenSheet: false,
                LowFraction: 20,
                MerchantName: '',
                RoomNum: ''
            },
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            isLoginStatus: false,
            poptipWav: require('../../assets/audio/4204.wav'),
            poptipMp3: require('../../assets/audio/4204.mp3'),
            isLogining: false
        }
    },
    beforeRouteLeave (to, from, next) {
        let that = this;

        that.lotterySignalr.closeSignalr();
        next();
    },
    computed: {
        showDataList() {
            let that = this;
            let start = (that.page.pageNum - 1) * that.page.pageSize;
            let end = that.page.pageNum * that.page.pageSize - 1;
            return that.dataList.slice(start, end);
        }
    },
    mounted() {
        let that = this;

        that.handleGetInfo();
        that.lotterySignalr.reStart(100);

        Bus.$on('LoginStatus', function(mes) {
            if (typeof mes == 'string')
                mes = JSON.parse(mes)
            console.log(mes)
            that.$Message.success(mes.Message);
        })

        Bus.$on('TargetInfo', function(mes) {
            if (typeof mes == 'string')
                mes = JSON.parse(mes)
            that.sync.handicap = mes.MerchantName;
            that.sync.account = mes.UserName;
            that.sync.balance = mes.Balance;
            that.sync.alreadyKnotBalance = mes.Already;
            that.sync.unfinishedBalance = mes.NAlready;
            that.sync.profitAndLoss = mes.Proloss;
            that.isLogining = false;
        })

        Bus.$on('MerchantBetSheet', function(mes) {
            if (typeof mes == 'string')
                mes = JSON.parse(mes);
            let _data = {
                username: mes.UserName,
                gameName: mes.GameType,
                perids: mes.Nper,
                currentBet: mes.Remark,
                bet: mes.UseMoney,
                time: mes.Time,
                status: mes.Status
            }
            that.dataList.unshift(_data);
            that.page.total = that.dataList.length;
        })

        Bus.$on('LowFraction', function(mes) {
            console.log(mes);
            let au = document.createElement('audio');
            au.preload = 'auto';
            au.src = that.poptipWav;
            au.src = that.poptipMp3;
            au.play();
        })

        Bus.$on('exitStatus', function() {
            that.$Message.success('退出成功!');
            that.isLoginStatus = false;
        })
    },
    methods: {
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.handleGetSheetInfos();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.periods = '';
            that.handleGetSheetInfos();
        },
        handleTipsLimit() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/TipsLimit',
                    params: {
                        num: that.flyInfo.LowFraction
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                }).catch(function(e) {
                    that.isLoading = false;
                })
            }
        },
        handleTipsStatus() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/TipsOpen',
                    params: {
                        open: that.flyInfo.Remind
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        if (that.flyInfo.Remind) {
                            that.handleTipsLimit();
                        }
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                }).catch(function(e) {
                    that.isLoading = false;
                })
            }
        },
        hanleSyncTime() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/SheetAutoCorrection',
                    params: {
                        islock: that.flyInfo.AutoSyn
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                }).catch(function(e) {
                    that.isLoading = false;
                })
            }
        },
        handleSyncInfo() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/SheetSynchronization',
                    params: {
                        time: that.flyInfo.AdvanceTime
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                }).catch(function(e) {
                    that.isLoading = false;
                })
            }
        },
        handleGetInfo() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/GetFlySheetInfo'
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.flyInfo.AdvanceTime = data.data.Model.AdvanceTime;
                        that.flyInfo.SeurityNo = data.data.Model.SeurityNo || '';
                        that.flyInfo.AutoSyn = data.data.Model.AutoSyn;
                        that.flyInfo.Password = data.data.Model.Password || '';
                        that.flyInfo.Remind = data.data.Model.Remind;
                        that.flyInfo.OpenSheet = data.data.Model.OpenSheet;
                        that.flyInfo.LowFraction = data.data.Model.LowFraction;
                        that.flyInfo.MerchantName = data.data.Model.MerchantName || '';
                        that.flyInfo.RoomNum = data.data.Model.RoomNum || '';
                    }
                }).catch(function(e) {
                    that.isLoading = false;
                })
            }
        },
        handleSwitch(item) {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/FlySingleOpen',
                    params: {
                        open: !item
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.flyInfo.OpenSheet = !item;
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                }).catch(function(e) {
                    that.isLoading = false;
                })
            }
        },
        handleLongExit() {
            let that = this;

            that.lotterySignalr.exitMonitor();
        },
        handleVerifyConnect() {
            let that = this;

            if (!that.flyInfo.SeurityNo.trim()) {
                that.$Message.error('请输入安全码!');
                return;
            }

            if (!that.flyInfo.RoomNum.trim()) {
                that.$Message.error('请输入房间码!');
                return;
            }

            if (!that.flyInfo.MerchantName.trim()) {
                that.$Message.error('请输入登录账号!');
                return;
            }

            if (!that.flyInfo.Password.trim()) {
                that.$Message.error('请输入登录密码!');
                return;
            }

            if (!that.isLoading && !that.isLogining) {
                that.isLoading = true;
                that.isLogining = true;

                that.$axios({
                    url: '/api/Setup/AddSheetMerchant',
                    params: {
                        seurityNo: that.flyInfo.SeurityNo,
                        merchantName: that.flyInfo.MerchantName,
                        merchantPwd: that.flyInfo.Password,
                        roomNum: that.flyInfo.RoomNum
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.isLoginStatus = true;
                        that.flyInfo.userId = data.data.Model.UserID;
                        that.flyInfo.targetId = data.data.Model.TargetID;
                        that.lotterySignalr.joinMonitor(that.flyInfo.userId, that.flyInfo.targetId);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                }).catch(function(e) {
                    that.isLoading = false;
                })
            } else if (that.isLogining) {
                that.$Message.error('登录中,请稍后...');
            }
        }
    }
}
</script>

<style lang="scss">
.fly-alone {
    .content {
        flex-direction: column;

        .list-header {
            .list-title {
                justify-content: space-between;

                width: 100%;

                overflow: hidden;
                .button {
                    display: inline-block;
                    width: 80px;
                    height: 32px;
                    line-height: 32px;
                    text-align: center;
                    border-radius: 4px;
                    background-color: #0078ff;
                    cursor: pointer;
                    vertical-align: middle;
                    
                    & > span {
                        color: #ffffff;
                    }
                }
            }
        }

        .query-list {
            .connection-info {
                display: flex;
                justify-content: space-around;
                margin-top: 10px;
                padding: 8px 2px;
                
                border: solid 1px #e4e4e4;

                .connection-item {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    & > span {
                        margin-right: 3px;
                        word-break: keep-all;
                        font-size: 12px;
                    }
                }

                .button {
                    width: 60px;
                    height: 32px;
                    border-radius: 4px;
                    cursor: pointer;
                    
                    & > span {
                        color: #ffffff;
                    }
                }

                .login {
                    background-color: blue;
                }

                .exit {
                    background-color: red;
                }
            }

            .sync-info {
                display: flex;
                justify-content: space-around;
                margin-top: 10px;
                
                border: solid 1px #e4e4e4;

                .sync-item {
                    flex-grow: 1;
                    border-right: solid 1px #e4e4e4;

                    & > p {
                        height: 40px;
                        line-height: 40px;
                        text-align: center;
                    }

                    & > p:first-child {
                        border-bottom: solid 1px #e4e4e4;
                    }
                }
            }
        }

        .list-data {
            margin-top: 15px;

            width: 100%;

            border: solid 1px #d7d7d7;

            .data-header, .data-item {
                display: flex;
                align-items: stretch;

                height: 40px;

                background-color: rgb(228, 228, 228);

                .item-info {
                    width: 95px;
                    line-height: 40px;

                    border-right: solid 1px #c9c9c9;

                    .manual-lottery {
                        color: #03A9F4;
                        text-decoration: underline;
                        cursor: pointer;
                    }
                }

                .item-5 {
                    width: 335px;

                    overflow: hidden;
                    cursor: help;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }

                .item-7 {
                    width: 120px;
                }

                .item-8 {
                    border-right: 0;
                }
            }

            .data-con {
                min-height: 450px;

                overflow: auto;
                font-size: 12px;
                .data-item {
                    height: 35px;

                    background-color: #fff;
                    .item-info {
                        line-height: 35px;
                    }
                }

                .data-item:nth-child(2n+2) {
                    background-color: rgb(249, 249, 249);
                }
            }
        }
    }

    .other-nav {
        display: flex;
        align-items: center;
        justify-content: space-between;

        margin: 10px 0;

        height: 36px;

        background-color: #f2f2f2;
        border-radius: 4px;
        overflow: hidden;

        .nav-item {
            padding: 0 20px;

            line-height: 36px;

            cursor: pointer;
        }
    }
}
</style>